.login{
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login__container{
    width: 300px;
    display: flex;
    flex-direction: column;
    padding:20px;
    border: 1px solid lightgray;

}
.login__logo{
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100px;
    object-fit: contain;
}
.login__container > h1{
    font-weight: 500;
    margin-bottom: 20px;

}
.login__container > form >h5{
    margin-bottom: 5px;
}
.login__container > form > input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width:98%;
}
.login__signInButton{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 blue black;
}
.login__registerButton{
    
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 blue black;
}